





import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import RepositoryEditor from "@/components/misc/RepositoryEditor.vue";
import { Overwrite } from "@/shared";
import { IRepositoryWithClientId } from "@/store/modules/AdminModule";
import { IRepository } from "@/store/modules/RepositoriesModule";
import { repositoriesModule, routerModule, adminModule } from "@/store/modules";

@Component({
  components: {
    RepositoryEditor
  }
})
export default class NewRepositoryView extends Mixins() {
  path = "";
  async mounted() {
    await this.clientpath();
  }
  get clientId(): string {
    return routerModule.params.clientId;
  }

  async clientpath() {
    /*const data = await adminModule.getClientInfo(this.clientId);
    if (data) {
      if (data.payload) {
        this.path =  "\\\\" + data.payload.client.ip + "\\c$";
      }
    }*/
  }

  async onSubmit(
    repo: Overwrite<
      IRepository,
      {
        id?: number;
      }
    >
  ) {
    // TODO: infer args from other class
    // TODO: trycatch
    if (!this.clientId) {
      const resp = await repositoriesModule.createRepository(repo);
      if (resp && resp.payload) {
        this.$router.replace({
          path: `/repositories/${resp.payload.repo.id}`
        });
      }
    } else {
      const clientrepo: IRepositoryWithClientId = {
        clientId: this.clientId,
        clientrepo: repo
      };
      const resp = await adminModule.createRepoOnClient(clientrepo);
      if (resp && resp.payload) {
        this.$router.replace({
          path: `/admin/client/${this.clientId}`
        });
      }
    }
  }
}
